<template>
    <el-container id="main" style="height: 100%;">
        <div class="guide-div" @click="enterPage">
            <p class="guide-p1">“知他”共享智能平台</p>
            <p class="guide-p2">科技部重点研发计划资助项目（2018YFB1403000）</p>
            <img src="static/images/guide_img1.png" class="guide-img1" alt="">
            <vue-seamless-scroll :data="imgList1" :class-option="classOption" class="img-content">
                <div class="one-img" v-for="item in imgList1">
                    <img :src="`static/images/`+item+`.png`" alt="">
                </div>
            </vue-seamless-scroll>
            <img src="static/images/guide_img2.png" class="guide-img2" alt="">
            <vue-seamless-scroll :data="imgList2" :class-option="classOption2" class="img-content2">
                <div class="one-img" v-for="item in imgList2">
                    <img :src="`static/images/`+item+`.png`" alt="">
                </div>
            </vue-seamless-scroll>
        </div>
    </el-container>
</template>
<style>
    @import "../../static/css/guide.css";
</style>
<script>
import $ from "jquery";

export default {
    name: "guide",
    data: function() {
        return {
            imgList1:['dev1','dev2','dev3','dev4','dev5','dev6','dev7','dev1','dev2','dev3','dev4','dev5','dev6','dev7'],
            imgList2:['cop1','cop2','cop3','cop4','cop5','cop6','cop1','cop2','cop3','cop4','cop5','cop6'],
            identity:Cookies.get("fedrole"),
        }
    },
    computed: {
        classOption () {
            return {
                singleWidth: 0,
                waitTime: 0,
                step: 2,
                hoverStop: false,
                direction:2,
            }
        },
        classOption2 () {
            return {
                singleWidth: 0,
                waitTime: 0,
                step: 2,
                hoverStop: false,
                direction:3,
            }
        },

    },
    methods: {
        enterPage:function(){
            var that=this;
            if(that.identity=="平台管理员"){ //平台管理员
                setTimeout(function(){
                    that.$router.push('/nodeManage')
                },1000);
            }
            else if(that.identity=="节点管理员"){ //节点管理员
                setTimeout(function(){
                    that.$router.push('/nodeSet')
                },1000);
            }
            else if(that.identity=="节点普通用户"){ //节点普通用户
                setTimeout(function(){
                    that.$router.push('/dataManage')
                },1000);
            }
        },
    },
};
</script>




